import { Component, OnInit, HostListener, ViewChild } from '@angular/core';

import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';

import { AccountsService } from 'src/app/api/controllers/Accounts';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-income-view-defaulters',
  templateUrl: './income-view-defaulters.component.html',
  styleUrls: ['./income-view-defaulters.component.scss']
})
export class IncomeViewDefaultersComponent implements OnInit {
  
  isDefaultersDataLoaded:boolean = false;

  defaultData:string = "";
  defaultDataList:any;

  totalItems:any;

  userDataList:any;
  glAccountListData:any;

  isDefaultSelected:boolean = false;
  selectAllDefaulters:boolean = false;

  sendMessage:string = null;

  gridSourceData:any;
  gridDefaultDataList:any;
  columnData:any;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  constructor(
  	private accountsService:AccountsService,
  	private cookieService:CookieService
  ) { }

  onSearchFilter(){

    if(this.defaultData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.defaultData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }

  getPrintParams(event){
    this.datagrid.exportdata(event, 'helpdeskData');
  }

  @HostListener('window:onCheckDefaulterHeader', ['$event.detail'])
  onCheckDefaulterHeader(detail) {

    let allDataRecords = this.datagrid.getrows()

    _.each(allDataRecords, item => {
      item.checked = detail.checked
    })

    this.isDefaultSelected = allDataRecords.some(item => {
      return item.checked
    })
    this.datagrid.refresh();

  }

  @HostListener('window:onCheckDefaulterRow', ['$event.detail'])
  onCheckDefaulterRow(detail) {
    
    let allDataRecords = this.datagrid.getrows()
    var dataRecord = this.datagrid.getrowdata(detail.rowId)

    dataRecord.checked = detail.checked;

    this.isDefaultSelected = allDataRecords.some(item => {
      return item.checked
    })

  }


  sendEmail() {
    
  }

  sendSMS() {
    
  }

  isItemsAvailable(){
    return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable(){
    return this.totalItems == 0 ? true : false;
  }

  renderColumns() {

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }

    this.columnData = [{
         text: '',
         datafield: 'checked',
         width: 80,
         pinned: true,
         sortable: false,
         menu: false,
         cellsrenderer: (row, column, value) => {

            var chkedProperty;
            
            if(value){
              chkedProperty = 'checked="true"';
            }
            else {
              chkedProperty = '';
            }

            return '<div class="jqx-custom-inner-cell">'
                    + '<div class="form-group mb-0 w-100">'
                      +  '<div class="form-check text-center">'
                          + '<input type="checkbox" class="form-check-input" onClick="checkDefaulterRowEvent('+ row +', this.checked)" id="defaultChecker'+ row +'" name="defaultChecker'+ row +'" '+ chkedProperty +'>'
                          + '<label class="form-check-label" for="defaultChecker'+ row +'"></label>'
                      +  '</div>'
                    + '</div>'
                 + '</div>'
         },
         renderer: (value) => {

          return '<div style="padding: 10px">'
          + '<div class="form-group mb-0 w-100">'
            +  '<div class="form-check check-header text-center">'
                + '<input type="checkbox" id="selectAllDefaulters" name="selectAllDefaulters" onClick="checkDefaulterHeaderEvent(event, this.checked)" >'
                + '<label class="form-check-label" for="selectAllDefaulters"></label>'
            +  '</div>'
          + '</div>'
       + '</div>'
         }
     },
     {
         text: 'Type',
         datafield: 'type',
         width: 200,
         cellsrenderer: cellsrenderer,
         renderer: columnrenderer
     },
     {
         text: 'Tower Unit',
         datafield: 'unit',
         minwidth: 100,
         cellsrenderer: cellsrenderer,
         renderer: columnrenderer
     },
     {
      text: 'Primary Contact',
      datafield: 'primaryContact',
      minwidth: 150,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
     },
     {
      text: 'Phone',
      datafield: 'phone',
      minwidth: 180,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
     },
     {
      text: 'Amount',
      datafield: 'due',
      minwidth: 150,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
     },
     {
      text: 'Email',
      datafield: 'emailSetting',
      minwidth: 200,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
     }];

     this.isDefaultersDataLoaded = true;

  }

  ngOnInit() {

    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

  	this.accountsService.getIncomeTrackerDefaulterByApartmentId(params).subscribe((res:any) => {

      var gridDefaultDataList = res;

      _.each(gridDefaultDataList, item => {
        item.checked = false;
      })

      this.totalItems = gridDefaultDataList.length;
      
      this.gridSourceData = {
        localdata: gridDefaultDataList,
        datatype: "array"
      }


      this.defaultDataList = new jqx.dataAdapter(this.gridSourceData);
      this.renderColumns();

  	}, error => {
    	console.log(error);
    });

  }

}


function checkDefaulterHeaderEvent(event, isChecked){
  
  event.stopPropagation();
  var newEvent = new CustomEvent('onCheckDefaulterHeader', {
    detail : {
      checked: isChecked
    }
  })
  window.dispatchEvent(newEvent);

}
(window as any).checkDefaulterHeaderEvent = checkDefaulterHeaderEvent;

function checkDefaulterRowEvent(row, isChecked){

  var event = new CustomEvent('onCheckDefaulterRow', {
    detail: {
        rowId: row,
        checked: isChecked
    }
  })
  window.dispatchEvent(event);
}
(window as any).checkDefaulterRowEvent = checkDefaulterRowEvent;
